import React, { Component } from "react";
import Filter from "./Filter";
import AllProduct from "./AllProduct";
import Cart from "./Cart";
import Product from '../data';
import alertify from 'alertifyjs';

alertify.set('notifier','position', 'top-right');

export default class ShoppingCart extends Component {
  state = {
      products: Product,
      cart: []
  }
  addToCart = (product) => {
    const { cart } = this.state;
    let isExistProduct = cart.findIndex(item => Object.is(item.id, product.id));
    if(isExistProduct === -1) {
      alertify.notify(`Đã thêm ${product.quantity} sản phẩm này vào giỏ hàng!`, 'success', 2);
       return this.setState({
          cart: [...this.state.cart, product]
      });
    }
    let newCart =  cart.map(item => Object.is(item.id, product.id) ? {...item, quantity: (item.quantity + 1) }:  item ); 
    this.setState({
      cart:  newCart
    }, () => {  alertify.notify(`Đã thêm ${newCart[isExistProduct].quantity} sản phẩm này vào giỏ hàng !`, 'success', 2)});
  }
  removeCart = () => {
    this.setState({ cart: [] });
    alertify.notify(`Bạn đã thanh toán thành công! Chúc bạn 1 ngày tốt lành`, 'success', 5);
  }
  removeCartItem = (id) => {
    let arr = [...this.state.cart];
    arr.splice(id, 1);
       this.setState({
          cart: arr
       })
  }
  render() {
    const { products, cart } = this.state;
    return (
      <div>
        <div className="container">
          <div className="row">
            <div className="col-md-3">
              <div className="filter">
                <h3>Sizes:</h3>
                <div className="size">S</div>
                <div className="size">M</div>
                <div className="size">L</div>

                <div className="size">XL</div>
              </div>
            </div>
            <AllProduct products={products} addToCart={this.addToCart}/>
          </div>
        </div>
         <Cart carts={cart} removeCart={this.removeCart} removeCartItem={this.removeCartItem} />
      </div>
    );
  }
}
