import React from 'react';

export default function Product({ item: { id, name, price, src }, addToCart}) {
     const addToCart1 = e => {
         e.preventDefault();
         addToCart({ id, name, price, src, quantity: 1 });
     }
     return (
        <div className="col-md-4 ">
            <div className="product">
                <img
                    className="product-img"
                    src={src}
                    alt={name}
                />
                <h6 className="product-title">{name}</h6>
                <p className="product-price">{price}$</p>
                <a href="#" className="add-cart-btn" onClick={addToCart1}>
                    Add to Cart
                </a>
            </div>
      </div>
     );
}