import React from "react";
import CartItem from "./CartItem";
import alertify from 'alertifyjs';
export default class Cart extends React.Component {
  state = {
     isShowCart: false
  }

  _handleClick = () => {
     this.setState({
        isShowCart: !this.state.isShowCart
     })
  }
  
  _handleTotal = (e) => {
       e.preventDefault();
      let isBuy = !this.props.carts[0] ? alertify.notify('Không có sản phẩm nào để thanh toán. Vui lòng chọn sản phẩm cần mua', 'error', 3) : window.confirm('Bạn có chắc chắn đã mua xong và bắt đầu thanh toán sản phẩm ? ');
      console.log(isBuy)
      if(isBuy === true) {
        this.props.removeCart();
      }
  }

  render() {
    return (
      <>
        <div className="cart-quantity outside" onClick={ this._handleClick }>
          <div className="cart-img">
            <div className="cart-number">{ this.props.carts.length }</div>
          </div>
        </div>
        <div className={`cart ${ this.state.isShowCart ? 'active': '' }`}>
          <div className="close-cart" onClick={ this._handleClick }>X</div>
            <div className="title">
              <div className="cart-quantity">
                  <div className="cart-img"> 
                    <div className="cart-number">{ this.props.carts.length }</div>
                  </div>
              </div>
               <h4>Cart</h4>
          </div>
          <hr />
          
        <div className="cart-items">
              { !this.props.carts[0] ?  <span style={{color: 'white',fontSize: '27px', textAlign: 'center', display: 'block'}}>Your Cart To Empty</span> : this.props.carts.map(item => {
                  return <CartItem key={item.id}  cartItem={item} removeCartItem={this.props.removeCartItem} />
              })}
        </div>
          <a href="/" className="checkout-btn" onClick={this._handleTotal}>
                  <span  style={{color: 'red'}}>Thanh Toán: </span>  { this.props.carts.reduce((total, item)=> {
                      return total + (item.price * item.quantity)
              },0)} $
          </a>  
    </div>
      </>
    );
  } 
}
