import React from 'react';

export default function CartItem({ cartItem: { id, name, price, src, quantity }, removeCartItem }) {
     function handleClick() {
        removeCartItem(id);
     }
     return (
        <div className="cart-item d-flex">
            <img
                src={ src }
                alt={name}
            />
            <div className="cart-item-info">
                <h5 className="cart-item-title">{name}</h5>
                <p className="cart-item-size nam">Size: X</p>
                <p className="cart-item-quantity nam">Quantity: {quantity}</p>
            </div>
            <div className="cart-item-price">{price}$</div>
            <div className="remove-cart-item">
                <i className="fas fa-times" onClick={ handleClick}/>
            </div>
        </div>
     );
}